







































import { Component, Vue } from 'vue-property-decorator';
import { Field,Row,Col,Button } from 'vant';
import ScanSign from '@/componets/scan_sign.vue';
import {notes, NoteType} from '@/note';


import 'vant/lib/field/style';
import 'vant/lib/row/style';
import 'vant/lib/col/style';
import 'vant/lib/button/style';

Vue.use(Field);
Vue.use(Row);
Vue.use(Col);
Vue.use(Button);

@Component({
    components:{ScanSign},
})

export default class Input extends Vue {
    private scanSign=false;
    private ownCert='test';
    private  inputMessage='';
    private showConfirm=true;

    public created(): void {
        this.$store.state.title = this.$t('own.input');
    }
    private scanOwn():void{
        if(this.inputMessage){
            if(this.scanSign){
                this.scanSign=false;
                this.showConfirm=true;
            }else{
                this.scanSign=true;
                this.showConfirm=false;
            }
        }else {
            notes.add({type: NoteType.Error, message: '输入为空！'});
        }

    }
    private confirmOwn():void{
        if(this.inputMessage){
            this.$router.push({name:'own_confirm',params:{inputMsg:this.inputMessage}})
        }else {
            notes.add({type: NoteType.Error, message: '输入为空！'});
        }
    }
    private scanSignedOwn():void{

    }

}
